iframe{
    width: 540px;
    height: 450px;
}

/* mobile */
@media only screen and (max-width: 768px) {
    iframe{
        width: 340px;
        height: 250px;
    }
}

/* tablets */
@media only screen and (min-width: 600px) {
    iframe{
        width: 540px;
        height: 450px;
    }
}