.App {
  text-align: center;
}

nav{
  display: inline-block;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: black;
  text-align: center;
  padding: 10px;
  overflow: visible;
}

nav li {
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: middle;
  font-size: 25px;
  color: bisque;
  padding: 5px;
}

nav li a{
  padding: 14px 16px;
  color: bisque;
}

nav a {
  color: bisque;
}

nav a:visited {
  color: bisque;
}
nav a:hover {
  color: red;
}

/* mobile */
@media only screen and (max-width: 768px) {
  nav li{
    font-size: 20px;
  }

  th{
    font-size: 15px;
  }

  nav li a{
    padding: 10px 5px;
  }
}

